	module.exports = {"data":{"nodePost":{"title":"Flip Grip Mini Review","body":{"value":"<p>'Tis the season for all of the random Nintendo Switch related Kickstarter projects I backed throughout the year to show up in advance of the holidays.</p>\r\n<p>The <a href=\"https://www.kickstarter.com/projects/fangamer/flip-grip\">Flip Grip</a> is a simple, but extremely well designed piece of plastic that allows you to play Switch in vertical mode. Didn't know that vertical mode was a thing? A small subset of often retro games support it. To try out the grip I picked up <a href=\"https://www.nintendo.com/games/detail/arcade-archives-donkey-kong-switch\">Arcade Archives: Donkey Kong</a>, <a href=\"https://www.nintendo.com/games/detail/namco-museum-switch\">Namco Museum</a>, and <a href=\"https://www.nintendo.com/games/detail/pinball-fx3-switch\">Pinball FX3</a>. All told, I spent much more on games than the $12 that the grip costs.</p>\r\n<p>Overall, it works extremely well for the games that support vertical mode. It isn't something that I'll use that often, but I love having yet another configuration of this versatile device. Fingers crossed that a version of Ms. Pac-Man that supports vertical mode is released at some point.</p>  "},"relationships":{"image":{"localFile":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.0769230769230769,"src":"/static/44f040f8156059abc476919c742536e9-ac3a754436b2ce0ac1fb7dc763288156-42807.jpg","srcSet":"/static/44f040f8156059abc476919c742536e9-ac3a754436b2ce0ac1fb7dc763288156-960f9.jpg 313w,\n/static/44f040f8156059abc476919c742536e9-ac3a754436b2ce0ac1fb7dc763288156-c36f2.jpg 625w,\n/static/44f040f8156059abc476919c742536e9-ac3a754436b2ce0ac1fb7dc763288156-42807.jpg 1250w,\n/static/44f040f8156059abc476919c742536e9-ac3a754436b2ce0ac1fb7dc763288156-dbc5e.jpg 1875w,\n/static/44f040f8156059abc476919c742536e9-ac3a754436b2ce0ac1fb7dc763288156-6e234.jpg 2500w,\n/static/44f040f8156059abc476919c742536e9-ac3a754436b2ce0ac1fb7dc763288156-a7d90.jpg 3024w","sizes":"(max-width: 1250px) 100vw, 1250px"}}}}},"created":1544108145}},"pathContext":{"slug":"/post/flip-grip-mini-review/"}}

